'use client'
import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { menuItems } from "./ menuLIst";
import { motion } from "framer-motion";
import { useState, useEffect } from "react";
import { MoveRight } from "lucide-react";
const linkVariants = {
  hidden: { opacity: 0, x: -20 },
  visible: (i: number) => ({
    opacity: 1,
    x: 0,
    transition: {
      duration: 0.4,
      delay: i * 0.1, // Staggered delay for each link
      ease: "easeOut",
    },
  }),
};
const socialLinks = [
  { icon: "f", link: "https://www.facebook.com/GenZGlobalEducation", label: "Facebook" },
  { icon: "instagram", link: "https://www.instagram.com/genzglobaledu/", label: "Instagram" },
  { icon: "tiktok", link: "https://www.tiktok.com/@genzglobal", label: "tiktok" },
  { icon: "linkedin", link: "https://www.linkedin.com/company/genzglobal/", label: "linkedin" },

];

const Navbar = () => {
  const pathname = usePathname();
  const [isOpen, setIsOpen] = useState(false);
  const [openSubmenu, setOpenSubmenu] = useState<number | null>(null);
  const [isFixed, setIsFixed] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsFixed(window.scrollY > 200);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
    if (isOpen) setOpenSubmenu(null);
  };
  const toogleMenu = () => {
    setIsOpen(false);
  };
  const toggleSubmenu = (id: number) => {
    setOpenSubmenu(openSubmenu === id ? null : id);
  };

  return (
    <div>
      {/* Top Navbar */}
      <div className="bg-[#1A2A44] py-2 px-6 text-white">
        <div className="container mx-auto flex items-center justify-between">
          {/* Contact Info - Left Side */}
          <div className="flex items-center gap-4 text-sm">
            <div className="flex items-center gap-2">
              <svg
                className="w-4 h-4"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z"
                />
              </svg>
              <a href="tel:+015008798">+977-1-4502299</a>
            </div>
            <div className="hidden md:flex items-center gap-2">
              <svg
                className="w-4 h-4"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z"
                />
              </svg>
              <a href="mailto:Hello@genzglobal.edu.npm">
              Hello@genzglobal.edu.np
              </a>
            </div>
          </div>

          {/* Social Media Icons - Right Side */}
          <div className="flex space-x-4">
              {socialLinks.map((social, index) => (
  <motion.a
    key={social.label}
    href={social.link}
    target="_blank"
    rel="noopener noreferrer"
    initial="hidden"
    whileInView="visible"
    viewport={{ once: true }}
    variants={linkVariants}
    custom={index}
    className="text-gray-300 hover:text-[#00AEEF] transition-colors duration-300"
  >
    {social.icon === "f" ? (
      <svg
        className="w-6 h-6"
        fill="currentColor"
        viewBox="0 0 24 24"
      >
        <path d="M9 8h-3v4h3v12h5v-12h3.642l.358-4h-4v-1.667c0-.955.192-1.333 1.115-1.333h2.885v-5h-3.808c-3.596 0-5.192 1.583-5.192 4.615v3.385z" />
      </svg>
    ) : social.icon === "instagram" ? (
      <svg
        className="w-6 h-6"
        fill="currentColor"
        viewBox="0 0 24 24"
      >
        <path d="M12 2.163c3.204 0 3.584.012 4.85.07 3.252.148 4.771 1.691 4.919 4.919.058 1.265.069 1.645.069 4.849 0 3.205-.012 3.584-.069 4.849-.149 3.225-1.664 4.771-4.919 4.919-1.266.058-1.644.07-4.85.07-3.204 0-3.584-.012-4.849-.07-3.26-.149-4.771-1.699-4.919-4.92-.058-1.265-.07-1.644-.07-4.849 0-3.204.013-3.583.07-4.849.149-3.227 1.664-4.771 4.919-4.919 1.266-.057 1.645-.069 4.849-.069zm0-2.163c-3.259 0-3.667.014-4.947.072-4.358.2-6.78 2.618-6.98 6.98-.059 1.281-.073 1.689-.073 4.948 0 3.259.014 3.668.072 4.948.2 4.358 2.618 6.78 6.98 6.98 1.281.058 1.689.072 4.948.072 3.259 0 3.668-.014 4.948-.072 4.354-.2 6.782-2.618 6.979-6.98.059-1.28.073-1.689.073-4.948 0-3.259-.014-3.667-.072-4.948-.196-4.354-2.617-6.78-6.979-6.98-1.281-.059-1.69-.073-4.949-.073zm0 5.838c-3.403 0-6.162 2.759-6.162 6.162s2.759 6.163 6.162 6.163 6.162-2.759 6.162-6.163c0-3.403-2.759-6.162-6.162-6.162zm0 10.162c-2.209 0-4-1.79-4-4 0-2.209 1.791-4 4-4s4 1.791 4 4c0 2.21-1.791 4-4 4zm6.406-11.845c-.796 0-1.441.645-1.441 1.44s.645 1.44 1.441 1.44c.795 0 1.439-.645 1.439-1.44s-.644-1.44-1.439-1.44z" />
      </svg>
    ) : social.icon === "tiktok" ? (
      <svg
        className="w-6 h-6"
        fill="currentColor"
        viewBox="0 0 24 24"
      >
        <path d="M16.5 1.5c0 2.425 1.925 4.4 4.3 4.5v3.125a7.75 7.75 0 01-4.8-1.625V15.4c0 4.4-3.625 7.9-8.025 7.6C4.65 22.7 1.5 19.325 1.5 15.4c0-3.85 2.9-7.025 6.675-7.4V11C6.425 11.35 5.5 12.7 5.5 14.25c0 1.85 1.5 3.375 3.375 3.4 1.95.025 3.625-1.6 3.625-3.55V1.5h4z" />
      </svg>
    ) : social.icon === "linkedin" ? (
      <svg
        className="w-6 h-6"
        fill="currentColor"
        viewBox="0 0 24 24"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path d="M19 0h-14c-2.8 0-5 2.2-5 5v14c0 2.8 2.2 5 5 5h14c2.8 0 5-2.2 5-5v-14c0-2.8-2.2-5-5-5zm-11.5 19h-3v-10h3v10zm-1.5-11.3c-1 0-1.8-.8-1.8-1.8S5 4.1 6 4.1s1.8.8 1.8 1.8-.8 1.8-1.8 1.8zm13 11.3h-3v-5.6c0-1.4-.5-2.3-1.8-2.3-1 0-1.5.7-1.8 1.3-.1.3-.1.7-.1 1.1v5.5h-3v-10h3v1.3c.4-.6 1.2-1.5 2.9-1.5 2.1 0 3.7 1.4 3.7 4.4v5.8z" />
      </svg>
    ) : null}
  </motion.a>
))}

              </div>
        </div>
      </div>
      {/* Main Navbar */}
      <div className={isFixed ? "h-20 md:h-24 w-full" : ""} />
      <nav className={`py-4 px-6 w-full z-50 bg-white  transition-all duration-300 ${isFixed ? 'fixed top-0 left-0' : 'relative'}`}>
        <div className="container mx-auto flex items-center justify-between">
          {/* Logo */}
          <div className="flex items-center">
  <Link href="/">
    <Image
      src="/logo.png"
      alt="Gen Z Global Logo"
      width={0} // let the image scale responsively
      height={0}
      sizes="(max-width: 768px) 150px, 220px"
      className="h-auto w-[150px] md:w-[200px] lg:w-[220px] object-contain"
    />
  </Link>
</div>


          {/* Hamburger Icon for Mobile */}
          <div className="md:hidden">
            <button
              onClick={toggleMenu}
              className=" text-GenZ-secondary focus:outline-none"
            >
              <svg
                className="w-8 h-8"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                {isOpen ? (
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M6 18L18 6M6 6l12 12"
                  />
                ) : (
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M4 6h16M4 12h16M4 18h16"
                  />
                )}
              </svg>
            </button>
          </div>

          {/* Menu Items and Button - Desktop */}
          <div
            className={`${
              isOpen ? "flex" : "hidden"
            } md:flex flex-col md:flex-row items-center gap-6 md:gap-8 absolute md:static top-16 left-0 w-full md:w-auto bg-[#1A2A44] md:bg-transparent p-6 md:p-0 z-50 transition-all duration-300`}
          >
            {/* Menu Items */}
            <ul className="flex flex-col md:flex-row gap-6 md:gap-8 w-full md:w-auto">
              {menuItems.map((item) => (
                <div key={item.title} className="relative group">
                  {/* Main Menu Item */}
                  <div className="flex items-center justify-between">
                    <Link
                      href={item.path}
                      className="relative px-4 py-2 flex items-center gap-1"
                      onClick={toogleMenu}
                    >
                      <span
                        className={`relative z-10 text-lg font-medium transition-colors duration-300 ${
                          pathname === item.path
                            ? " text-white md:text-GenZ-secondary"
                            : " text-GenZ-primary group-hover:text-[#00AEEF]"
                        }`}
                      >
                        {item.title}
                      </span>
                      {/* Down Arrow for items with submenu (visible only on desktop) */}
                      {item.submenu && (
                        <svg
                          className="w-4 h-4 hidden md:block text-GenZ-primary group-hover:text-[#00AEEF] transition-colors duration-300"
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            d="M19 9l-7 7-7-7"
                          />
                        </svg>
                      )}
                    </Link>
                    {/* Submenu Toggle Button for Mobile */}
                    {item.submenu && (
                      <button
                        onClick={() => toggleSubmenu(item.id)}
                        className="md:hidden text-white focus:outline-none px-4 py-2"
                      >
                        <svg
                          className={`w-5 h-5 transition-transform duration-300 ${
                            openSubmenu === item.id ? "rotate-180" : ""
                          }`}
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            d="M19 9l-7 7-7-7"
                          />
                        </svg>
                      </button>
                    )}
                  </div>

                  {/* Submenu */}
                  {item.submenu && (
                    <div
                      className={`${
                        isOpen
                          ? openSubmenu === item.id
                            ? "block"
                            : "hidden"
                          : "absolute opacity-0 invisible group-hover:opacity-100 group-hover:visible"
                      } left-0 mt-2  w-[16rem] transition-all duration-300 z-50 md:bg-white md:rounded-lg md:shadow-lg md:border md:border-gray-200`}
                    >
                      <ul className="py-2">
                        {item.submenu.map((subItem) => (
                          <li key={subItem.title}>
                            <Link
                              href={subItem.path}
                              onClick={toogleMenu}
                              className={`block  buyer-4 py-2 px-3 transition-colors duration-200 ${
                                pathname === subItem.path
                                  ? " bg-GenZ-primary text-white"
                                  : "text-gray-800 md:hover:bg-GenZ-secondary md:hover:text-white"
                              } ${
                                isOpen
                                  ? "text-white hover:bg-[#00AEEF]"
                                  : "md:text-gray-800"
                              }`}
                            >
                              {subItem.title}
                            </Link>
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}
                </div>
              ))}
            </ul>

            {/* Book Appointment Button */}
            <Link href="/book-appointment">
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className="bg-GenZ-secondary text-white px-6 py-2 flex justify-center items-center gap-2 rounded-full font-medium text-lg shadow-md hover:bg-[#008BCF] transition-colors duration-300 w-full md:w-auto"
              >
                Book Appointment <MoveRight />
              </motion.button>
            </Link>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Navbar;
